.board-square {
  height: var(--piece-side);
  background: #80ac51;
  -webkit-transition: all 0.25s;
  -o-transition: all 0.25s;
  transition: all 0.25s;
}

.board-square--playing:hover {
  background: #ccc;
}

.board-square:hover.board-square--active {
  background: var(--active-color);
}

.board-square--dark {
  background: #678e49;
}

.board-square--active {
  background: var(--active-color);
}



